const globalOptions = require('./globalOptions');

const _M = '   ';
const _V = '       ';
const _S = '';
const _A = '';
const _F = '  ';
const _I = '     ';
const _E = '    ';
const _G = '';
const _D = '         ';
const _P = ' ';

function removeSpace(content) {
  return content.replace(/\s/g, '');
}

/** Syllable base reordering */
const BURMESE = '-';

const C = '-';
const LONG_C = '';
const SHORT_C = '';
// Medials
const M = removeSpace(_M);
// Dependent Vowel Signs
const V = removeSpace(_V);
// Sign Virama
const S = removeSpace(_S);
// Sign Asat
const A = removeSpace(_A);
// Dependent Various Signs
const F = removeSpace(_F);
// Independent Vowels, Various Signs 
const I = removeSpace(_I);
// Independent Vowels
const E = removeSpace(_E);
// THA GYI 
const G = removeSpace(_G);
// Digits
const D = removeSpace(_D);
// Punctuation Marks
const P = removeSpace(_P);
// NUMBER_ZERO
const WA_LONE = '';
const NUMBER_ZERO = '';

const REGEX_BURMESE = new RegExp(`[${BURMESE}]`);

// Brake point used to reorder
const brakePoint = `([${C}${E}${NUMBER_ZERO}])([${M}${V}${A}${F}]+)`;
const brakePointRegex = new RegExp(brakePoint, 'gm');

/** Extended rules */
let extendedRules = [];
let postExtendedRules = [];
function addReplacementRule(rulesset, [pattern, replacement]) {
  rulesset.push([new RegExp(pattern, 'gm'), replacement]);
}

const NUMBER_ZERO_WA = ' ';                // Zero to Wa lone
const U_FIX = ' ';                         // U
const AWL = ' ';                      // Awl
// Debatable 
const DOUBLE_LONE_GYI_TIN = ' ';
const DOUBLE_TA_CHAUNG_NGIN = ' ';
const ZA_MYIN_ZWAE = ' ';

// Post fix rule
const SPACE_IN_FRONT_OF_VIRAMA = `([${SHORT_C}])\\s([-]) $1$2`;

addReplacementRule(extendedRules, NUMBER_ZERO_WA.split(' '));
addReplacementRule(extendedRules, U_FIX.split(' '));
addReplacementRule(extendedRules, AWL.split(' '));
addReplacementRule(extendedRules, DOUBLE_LONE_GYI_TIN.split(' '));
addReplacementRule(extendedRules, DOUBLE_TA_CHAUNG_NGIN.split(' '));
addReplacementRule(extendedRules, ZA_MYIN_ZWAE.split(' '));

addReplacementRule(postExtendedRules, SPACE_IN_FRONT_OF_VIRAMA.split(' '));

/** Extended rules */
const rankingMap = {
  // M
  '': 1,
  '': 2,
  '': 3,
  '': 4,
  // V 
  '': 5,
  '': 6,
  '': 7,
  '': 8,
  '': 9,
  // A
  '': 13,
  '': 10,
  '': 11,
  '': 12,
  // F
  '': 14,
  '': 15,
  '': 16,
};

// Remove duplicated
function uniquify(array) {
  return [...(new Set(array))];
}

// Apply replecement rules
function applyReplacementRules(rulesset, content) {
  return rulesset.reduce((a,b) => {
    return a.replace(b[0], b[1]);
  }, content);
}

const NON_NUMBER_BEHIND = new RegExp(`${S}$`)
const NON_NUMBER_AHEAD_SIGN = new RegExp(`^\s?[${M}${V}${S}${A}${F}]`);
const NON_NUMBER_AHEAD_C_SIGN = new RegExp(`^\s?[${C}][${S}${A}${F}]`);

/**
 * Fixing WaLone and YaGout
 * @param {String} text Content
 */
function fixWaAndYa(text = '') {
  function ruleFunction([num, char]) {
    const isWa = char === WA_LONE;
    return (behind, ahead) => {
      let isNumber = isWa;

      if (NON_NUMBER_BEHIND.test(behind)) {
        isNumber = false;
      }
      
      // is ya gout
      if (!isWa) {
        if (/[-=+-/]\s?$/.test(behind) && /^\s|\s?[-=+-/]/.test(ahead)) {
          isNumber = true;
        }

        if (/[-]|\s?[=+-/]/.test(ahead)) {
          isNumber = true;
        }
      }

      if (NON_NUMBER_AHEAD_SIGN.test(ahead) || NON_NUMBER_AHEAD_C_SIGN.test(ahead)) {
        isNumber = false;
      }
      
      //  but not 
      if (isWa && /^\s?/.test(ahead) && !/[-]\s?$/.test(behind)) {
        isNumber = false;
      }

      return behind + (isNumber ? num : char) + ahead;
    }
  }

  return text.split(/|/)
    .reduce(ruleFunction(['', '']))
    .split(/|/)
    .reduce(ruleFunction(['', '']));
}

/**
 * Normalization basically do transforming text into a single canonical form 
 * @param {String} content Context to normalize
 */
function normalize(content) {
  if (!content) {
    if (!globalOptions.isSilentMode()) console.warn('Content must be specified on knayi.normalize.');
    return '';
  }

  const result = content
    .replace(/[\u200B\u200C]/g, '')
    .replace(brakePointRegex, (m, g1, g2) => {
      let chunk = g1 || '';

      // Re-ordering
      uniquify(g2)
        .sort((a,b) => rankingMap[a] - rankingMap[b])
        .forEach((v) => chunk += v);
     
      return applyReplacementRules(extendedRules, chunk);
    });
  
  let clearText = applyReplacementRules(postExtendedRules, result);
  return fixWaAndYa(clearText);
}

module.exports = normalize;